.title__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.title__top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.title__svg {
  width: 25px;
  height: 25px; }

.title__svg__path {
  stroke-width: 5; }

.title__accent, .title__accent--clear {
  margin: 0;
  padding-top: 0.75em;
  font-size: 0.75em;
  font-weight: 300;
  color: #263745; }

.title__accent--clear {
  opacity: 0; }

.title__main {
  font-weight: 700;
  font-size: 5em;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.05em; }

.title__font--dark {
  color: #263745; }

.title__font--light {
  color: #eff8ff; }

.title__stroke--dark {
  stroke: #263745; }

.title__stroke--light {
  stroke: #eff8ff; }
